\n@use '../utilities/type-scale' as *;\n@use '../utilities/color' as *;\n\n:root {\n  // Fonts\n  & {\n    @each $font, $name in $fonts {\n      --font-#{$font}: #{$name};\n    }\n  }\n\n  // Colors\n  & {\n    @each $color, $shades in $colors {\n      @each $shade, $value in $shades {\n        --clr-#{$color}-#{$shade}: #{$value};\n      }\n    }\n  }\n\n  // Type-scale\n\n  @each $screen-size, $font-size in $type-scale {\n    & {\n      @each $size, $value in $font-size {\n        --fs-#{$size}: #{$value};\n      }\n    }\n  }\n\n  // Shadows\n  & {\n    @each $name, $value in $shadows {\n      --shw-#{$name}: #{$value};\n    }\n  }\n}\n","/* RESET */\n\n*,\n*::before,\n*::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  line-height: 1.5; /* You shouldn't use this line-height on headings */\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ntable {\n  border-collapse: collapse;\n  width: 100%;\n}\n\nth,\ntd {\n  padding: 0.25rem;\n  text-align: left;\n  border: 1px solid #ccc;\n}\n\ninput,\nbutton,\ntextarea,\nselect,\nlabel {\n  font: inherit;\n  font-size: max(16px, 1rem);\n  /* !Never set font-size less than 16px on these elements because of mobile zooming */\n}\n\nlabel,\ninput,\nselect,\nprogress,\nmeter {\n  display: block;\n  width: 100%;\n  padding: 5px;\n  height: 30px;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n","@use '../utilities' as *;\n\nhtml {\n  overflow-x: hidden;\n}\n\nbody {\n  font-family: var(--font-regular), sans-serif;\n  background-color: var(--clr-neutral-200);\n}\n\n.container {\n  max-width: toRem(1170);\n  margin: 0 auto;\n}\n\n.logo {\n  color: var(--clr-neutral-300);\n  font-size: var(--fs-500);\n  font-family: var(--font-bold);\n  letter-spacing: 1px;\n\n  @include respond-to(medium) {\n    font-size: var(--fs-400);\n  }\n\n  &-link:link,\n  &-link:visited {\n    text-decoration: none;\n  }\n}\